.container {
  width: 1248px;
  margin-left: auto;
  margin-right: auto;
  padding: 0px 32px;
  box-sizing: border-box;
}

.header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 70px;
  box-sizing: border-box;
  color: rgb(253, 253, 253);
  position: fixed;
  /*top: 0;
  left: 0; */
  /* width: 100%; */
  z-index: 99999;
}

.logoLink {
  text-decoration: none;
}

.header h1 {
  font-family: 'Manrope';
  font-size: 26px;
  line-height: 32px;
  margin-right: 723px;
  z-index: 99999;
  font-family: 'Manrope';
  font-weight: 500;
  color: rgba(255, 255, 255, 1);
}

.link h2 {
  font-family: 'Manrope';
  font-weight: 500;
  color: rgba(255, 255, 255, 1);
}

.navlinks {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999999;
}

.link {
  /* text-decoration: none; */
  color: rgb(255, 255, 255);
  font-size: 12px;
  line-height: 26px;
  margin-right: 40px;
  z-index: 9999999;
}
